* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.label, .value {
  color: #fff;
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #db1a1a, 0 0 82px #db1a1a, 0 0 92px #db1a1a, 0 0 102px #db1a1a, 0 0 151px #db1a1a;
}

.timer {
  margin-top: 50px;
  margin-left: 15px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.field:not(:last-child) {
  margin-right: 20px;
}

.value {
  font-size: 40px;
}

.label {
  font-size: 20px;
  font-weight: bold;
}

